*,
*:before,
*:after {
  box-sizing: border-box;
}
* {
  margin: 0;
  padding: 0;
  font: inherit;
}
img,
picture,
svg,
video {
  display: block;
  max-width: 100%;
}
input {
  outline: none;
  border: none;
}
a {
  text-decoration: none;
  color: inherit;
}
body {
  min-height: 100vh;
}
